import React, { useState, useEffect } from 'react';
import { Icon, Button, Modal, Spin } from 'antd';
import numeral from 'numeral';
import Signup from './../../../containers/Auth/Signup';

import { getSuscriptionsActives } from './../../../services';
import './../index.css';
import Scroll from 'react-scroll';

const scroller = Scroll.scroller;

const becomePeriod = value => {
  switch (value) {
    case 1:
      return 'Month';
    case 3:
      return 'Quarter';
    case 6:
      return '6 Month';
    case 12:
      return 'Annual';
    case 24:
      return 'Biannual';

    default:
      break;
  }
};

const PricingTable2 = ({ data, handleCancel, showModal }) => {
  const scrollTo = id => {
    scroller.scrollTo(id, {
      duration: 1500,
      delay: 100,
      smooth: true,
    });
  };

  return (
    <article
      style={{
        textAlign: 'left',
      }}
      className={`pricing-table-v1 ${data.recomended &&
        'pricing-table-highlight ribbon-container ribbon-primary'}`}
    >
      {data.recomended && (
        <div className="ribbon-wrapper">
          <div className="ribbon"> Popular </div>
        </div>
      )}
      <header>
        {' '}
        <h2>{data.title}</h2>{' '}
      </header>
      <p className="pricing-price">
         {numeral(data.price).format('0.00')}
        <span>/{becomePeriod(data.period)}</span>{' '}
      </p>
      <p className="pricing-price-detail">
        {/* Aspernatur omnis nemo <br /> omnis porro cupiditate quaera! */}
      </p>
      <div className="pricing-plan-details">
        <ul>
          <li
            className="pointer"
            onClick={() => {
              scrollTo('scroll-PricePerAdditionalPage');
            }}
          >
            <Icon style={{ color: '#16dd16' }} type="check" /> {' '}
            {numeral(data.pricePerAdditionalPage).format('0.00')} Price Per Additional Page
          </li>
          <li
            onClick={() => {
              scrollTo('scroll-FreeDays');
            }}
          >
            <Icon style={{ color: '#16dd16' }} type="check" /> {data.freePages} Free Days
          </li>
          <li
            onClick={() => {
              scrollTo('scroll-Pages');
            }}
          >
            <Icon style={{ color: '#16dd16' }} type="check" /> {data.pages} Pages
          </li>
          <li
            onClick={() => {
              scrollTo('scroll-MaxUsers');
            }}
          >
            <Icon style={{ color: '#16dd16' }} type="check" /> {data.maxUsers} Max Users
          </li>
          <li
            onClick={() => {
              scrollTo('scroll-MaxClones');
            }}
          >
            <Icon style={{ color: '#16dd16' }} type="check" /> {data.maxClones} Max Clones
          </li>{' '}
          <li
            onClick={() => {
              scrollTo('scroll-MaxSharedEngines');
            }}
          >
            <Icon style={{ color: '#16dd16' }} type="check" /> {data.maxSharedEngines} Max Shared
            Engines
          </li>
          <li
            onClick={() => {
              scrollTo('scroll-MaxPrivateInstances');
            }}
          >
            <Icon style={{ color: '#16dd16' }} type="check" /> {data.maxPrivateInstances} Max
            Private Instances
          </li>
          <li
            onClick={() => {
              scrollTo('scroll-CanTrain');
            }}
          >
            <Icon
              style={{ color: data.canTrain ? '#16dd16' : '#ff0000' }}
              type={data.canTrain ? 'check' : 'close'}
            />{' '}
            Can Train
          </li>
          <li
            onClick={() => {
              scrollTo('scroll-CanUseAPI');
            }}
          >
            <Icon
              style={{ color: data.canUseApi ? '#16dd16' : '#ff0000' }}
              type={data.canUseApi ? 'check' : 'close'}
            />{' '}
            Can Use API
          </li>
          <li
            onClick={() => {
              scrollTo('scroll-CanUsePGB');
            }}
          >
            <Icon
              style={{ color: data.canUsePGB ? '#16dd16' : '#ff0000' }}
              type={data.canUsePGB ? 'check' : 'close'}
            />{' '}
            Can Use PGB
          </li>
        </ul>
      </div>
      <footer>
        <Button
          onClick={() => {
            showModal(data.id);
          }}
          className="btn-cta w-100"
        >
          Get it now
        </Button>
      </footer>
    </article>
  );
};

const Section = ({ dashboard }) => {
  const [suscriptions, setSuscriptions] = useState([]);
  const [visible, setVisible] = useState(false);
  const [loading, setLoading] = useState(false);
  const [suscription, setSuscription] = useState(null);

  useEffect(() => {
    fetchSuscriptions();
  }, []);

  const fetchSuscriptions = async () => {
    try {
      setLoading(true);
      const { data } = await getSuscriptionsActives();
      setSuscriptions(data);
      setLoading(false);
    } catch (error) {
      setLoading(false);
    }
  };

  const handleCancel = () => {
    setVisible(false);
  };
  const showModal = value => {
    if (!dashboard) {
      setSuscription(value);
      setVisible(true);
    } else {
    }
  };

  return (
    <Spin spinning={loading} tip="Loading...">
      <article className="article">
        <div className="row" style={{ justifyContent: 'center' }}>
          {suscriptions.map((item, index) => {
            return (
              <div key={`pt-${index}`} className="col-md-4 col-xsm-6 ">
                <PricingTable2 showModal={showModal} handleCancel={handleCancel} data={item} />{' '}
              </div>
            );
          })}
        </div>

        <Modal visible={visible} onCancel={handleCancel} footer={null} className="custom-modal-v1">
          <Signup suscription={suscription} handleCancel={handleCancel} />
        </Modal>
      </article>
    </Spin>
  );
};

export default Section;
